@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  display: -ms-grid;
  display: grid;
  place-items: center;
  background-color: lightblue;
  height: 100vh;
  font-family: "Nunito", sans-serif;
  font-weight: 700;
}

.content-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 4em;
  font-family: "Pacifico", sans-serif;
}

.header .title {
  font-size: 44px;
}

.header .subtitle {
  font-size: 36px;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.player-input {
  display: flex;
  justify-content: space-between;
}

.player-input input {
  width: calc(100% - 26px);
  padding-left: 0.25em;
  font-size: 18px;
}

.player-input label {
  width: 24px;
  font-size: 22px;
}

.player-input + .player-input {
  margin-top: 0.5em;
}

@-webkit-keyframes btnToRight {
  0% {
    -webkit-transform: rotate(0.5turn) translateX(100%);
            transform: rotate(0.5turn) translateX(100%);
  }
  100% {
    -webkit-transform: rotate(0.5turn) translateX(0);
            transform: rotate(0.5turn) translateX(0);
  }
}

@keyframes btnToRight {
  0% {
    -webkit-transform: rotate(0.5turn) translateX(100%);
            transform: rotate(0.5turn) translateX(100%);
  }
  100% {
    -webkit-transform: rotate(0.5turn) translateX(0);
            transform: rotate(0.5turn) translateX(0);
  }
}

@-webkit-keyframes btnToLeft {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes btnToLeft {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.btn {
  overflow: hidden;
  position: relative;
  padding: 0.25em 0.75em;
  font-size: 20px;
  -ms-grid-row-align: center;
  align-self: center;
  margin-top: 10px;
  color: white;
  background-color: #e97400;
  border-radius: 3px;
  -webkit-transition: color linear 100ms;
  transition: color linear 100ms;
}

.btn::after {
  content: "";
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.btn:hover {
  color: transparent;
}

.btn:hover::after {
  background-image: url(/tic-tac-toe/static/media/left-arrow.a07b5b8d.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  -webkit-transform: rotate(0.5turn);
          transform: rotate(0.5turn);
  -webkit-animation-name: btnToRight;
          animation-name: btnToRight;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-duration: 250ms;
          animation-duration: 250ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.btn--play:hover::after {
  -webkit-animation-duration: 200ms;
          animation-duration: 200ms;
}

.btn--change-players:hover::after {
  -webkit-animation-name: btnToLeft;
          animation-name: btnToLeft;
  -webkit-animation-duration: 300ms;
          animation-duration: 300ms;
}

.status {
  font-size: 20px;
}

.board {
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: (1fr)[3];
      grid-template-rows: repeat(3, 1fr);
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.25em;
  gap: 0.25em;
  width: 200px;
  height: 200px;
  border-radius: 4px;
  overflow: hidden;
}

.board button {
  background-color: rgba(0, 0, 0, 0.85);
  font-size: 36px;
  color: white;
}

.board.is-active button {
  cursor: default;
}

.board.is-inactive button {
  cursor: pointer;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  font-size: inherit;
}

table,
td,
th {
  vertical-align: middle;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.5;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

a {
  text-decoration: none;
}

button {
  padding: 0;
  border: 0;
  cursor: pointer;
}

button:focus {
  outline: none;
}

div {
  margin: 0;
  padding: 0;
}

select,
input {
  border: 0;
}

